@import url(https://fonts.googleapis.com/css2?family=Exo:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Exo:wght@700&display=swap);
.aboutTitle {
  text-align: center;
  font-family: 'Exo', sans-serif;
  color: darkblue;
  margin-top: 40px;
}

.aboutList {
  margin-bottom: 100px !important;
  margin-left: 15px !important;
}

.iconColor {
  background-color: darkblue;
}
.streamScreen {
  position: absolute;
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.canvasStyle {
  position: absolute;
  z-index: 2000;
  height: 100vh;
  width: 100%;
  visibility: hidden;
}
.capPhotoHolder {
  background-color: rgba(0, 0, 0, 0.6);
}

.photoBox {
  padding: 0px !important;
  border: none !important;
  background-color: transparent !important;
  box-shadow: none !important;
}

.photoImg {
  width: 100%;
}

.grid {
  position: absolute;
  z-index: 3000;
  height: 100vh;
  width: 100%;
  background-color: transparent;
}

.colorText {
  text-align: center;
  color: white;
}

/* Import google font lobster to be used for the title */

.homeTitle {
  text-align: center;
  background-color: #80808042;
  color: lightblue;
  font-family: 'Lobster', cursive;
  font-size: 40px;
  margin-top: 10px;
  z-index: 1;
  position: fixed;
  width: 100%;
  margin-top: 0px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.homeWavePic {
  height: 100vh;
  width: 100vw;
}

@media screen and (max-width: 600px) {
  .largePic {
    display: none;
  }
}

@media screen and (min-width: 600px) {
  .smallPic {
    display: none;
  }
}
.uploadContainer {
  height: 100vh;
  width: 100vw;
}

.uploadBtn {
  text-align: center;
  margin-top: 275px;
}

.uploadIcon {
  color: lightblue;
}

.uploadPrompt {
  text-align: center;
  margin-top: 0px;
  color: lightblue;
  font-family: 'Exo', sans-serif;
  font-size: 18px;
}
/* General custom styles for navbar */
.navBarHolder {
  width: 100%;
  position: fixed;
  bottom: 0;
  border-top: lightgrey solid 1px;
  padding-top: 10px;
  background-color: white;
}

/* Style for active page link */
.activeLinkIcon {
  color: darkblue !important;
  padding: 6px 60px 8px !important;
}

.activeLinkIcon::before {
  box-sizing: inherit !important;
}

.activeLinkIcon::after {
  box-sizing: inherit !important;
}

/* Style for inactive page link */
.inactiveLinkIcon {
  color: lightblue !important;
  padding: 6px 60px 8px !important;
}

.inactiveLinkIcon::before {
  box-sizing: inherit !important;
}

.inactiveLinkIcon::after {
  box-sizing: inherit !important;
}

/* .MuiBottomNavigationAction-label {
  opacity: 1 !important;
} */
body {
  margin: 0px !important;
}

Link {
  text-decoration: none;
}

a {
  text-decoration: none;
}

a:-webkit-any-link {
  color: grey;
}
